import React from 'react'
import { connect } from 'react-redux'
import Header from './Header'
import { logOut } from '../../redux/authReducer'
import { withRouter } from "react-router-dom";
import { compose } from 'redux';
import axios from 'axios'


class HeaderContainer extends React.Component {

    constructor(props) {
        super(props)
        this.props = props
        this.state = {
            photo: 'https://social-network.samuraijs.com/activecontent/images/users/14035/user-small.jpg?v=7',
        }
    }

    componentDidMount() {
        console.log('Component Mounted!')
        const instance = axios.create({
            withCredentials: true,
            baseURL: 'https://social-network.samuraijs.com/api/1.0/',
            headers: {
                'API-Key': '0956be17-9287-4aaa-8ddc-9d18425e88b9',
            }
        })
        instance.get('profile/' + this.props.userId, {}).then(res => {
            this.setState({ photo: res.data.photos.small })
            console.log(res.data.photos.small)
        })
    }

    componentWillUnmount() {
        console.log('component unmounted!')
    }

    render() {
        return <Header {...this.props} photo={this.state.photo} />
    }
}

const mapStateToProps = (state) => ({
    isLoggedIn: state.authReducer.isLoggedIn,
    login: state.authReducer.login,
    isFetching: state.authReducer.isFetching,
    userId: state.authReducer.userId
})

export default compose(
    withRouter,
    connect(mapStateToProps, { logOut })
)(HeaderContainer) 